import React, { Component } from 'react';
import Header from './header';
import Benefit from './Benefit';
import Countdown from 'react-countdown-now';
import { Link, Element } from 'react-scroll';
import '../styles/App.css';

class App extends Component {
  render() {
    const mediumUrl = 'https://medium.com/the-mission/ico-101-how-to-participate-in-an-ico-made-with-ethereum-cf57516183f6';
    return [
      <section className='main-section first' key='first-section'>
        <Header />
        <h1 className='title'>
          Petro is the <strong>best attempt</strong> to replace the Bolivar in Venezuela
        </h1>
        <p className='Countdown-promo'>
          Trust in Petro Now and get a 2x1 promo
        </p>
        <div className='Countdown'>
          <Countdown date='4 Jan 2018 12:00:00' />
        </div>
        <h3 className='subtitle'>
          100,000 PETRO = 1 Ether
        </h3>
        <Link
          to='why'
          spy={true}
          smooth={true}
          duration={500}
          className='petro-cta'>
            Why Petro ?
        </Link>
      </section>,
      <Element name='why' className='second' id='second' key='second-section'>
        <h3 className='section-title'>Why Petro ?</h3>
        <div className='benefits-container'>
          <Benefit
            title="No government control"
            icon="spy"
            description="Ethereum network backs Petro; the Venezuela government doesn't have control over Petro."
          />
          <Benefit
            title="Alternative currency to the bolivar"
            icon="bandcamp"
            description="Ready to use for the citizenship, send money to your family with Petro and exchange it for Ether or Bitcoin"
          />
          <Benefit
            title="Trust of people"
            icon="users"
            description="The trust of people set the Petro value. No Oil, no Diamond, no Gold, no Bolivar, just the people."
          />
          <Benefit
            title="Main goal"
            icon="trophy"
            description="Our plan is replacing the Bolivar in Venezuela for Petro and improve the Venezuelans quality of life with a better cryptocurrency."
          />
        </div>
      </Element>,
      <section className="second-benefits" key='third-section'>
        <div className="second">
          <h3 className='section-title-alt'>Technology behind Petro</h3>
          <div className='benefits-second-container'>
            <article className='benefit'>
              <h4 className='benefit-title'>1.- Ethereum as blockchain network</h4>
            </article>
            <article className='benefit'>
              <h4 className='benefit-title'>2.- ERC20 standard</h4>
            </article>
          </div>
        </div>
      </section>,
      <section className='third-wrapper' key='fourth-section'>
        <section className='third'>
          <h2>Receive 100,000 PETRO per Ether.</h2>
          <h3>Get Petro now sending Ether to the smart contract address: <br /><br />
            <a href='https://etherscan.io/address/0x02fd82cba3bae39484d5eb7f75b5f3d5f418c691' className="ether-address">
              0x02Fd82cBA3baE39484D5eB7f75B5f3D5F418c691
            </a>
          </h3>
          <p>Min contibution: 0.01 ETH</p>
          <p>Gas Limit: 200,000</p>
          <p>Petro is not an ICO, but has the same rules, so, <a href={mediumUrl}>learn how contribute to Petro</a>
          </p>
          <p>
            Help Petro, spreading this project through Medium posts, forum,
            Facebook, Twitter, so that the whole world knows about Petro.
          </p>
        </section>
      </section>,
      <footer key="footer">
        <a className="footer-anchor" href='https://github.com/getpetro/petro'>
          <i className='benefit-icon fa fa-github'/>
            Source Code
        </a>
      </footer>
    ];
  }
}

export default App;



